:root {
    --gallery-width: 1112px;
    --gallery-grid-gap: 10px;
    --gallery-picture-width: 250px;
    --gallery-picture-height: 250px;
    --gallery-picture-padding: 5px;
    
    --gallery-padding-top: 75px;
    --gallery-padding-side: 50px;
    
    --gallery-screen-height: 500px;
    --gallery-screen-max-width: 800px;
    --gallery-screen-close-handle-size: 44px;
    --gallery-screen-control-height: 110px;
    --gallery-screen-control-width: 35px;
    --gallery-screen-border-radius: 15px;
}

.gallery {
    display: block;
    width: 775px; /* 3* 250 + 5 * 5 px */
    margin: var(--section-vertical) auto;

}

.galleryPictureWrapper {    
    display: inline-block;
    position: relative;
    
    max-width: var(--gallery-picture-width);
    max-height: var(--gallery-picture-height);
    width: var(--gallery-picture-width);
    height: var(--gallery-picture-height);
    
    margin-left: var(--gallery-picture-padding);

    overflow: hidden;
    
    border: 1px solid #ddd;
    cursor: pointer;
}

.curtain { 
    position: absolute;
    top: var(--gallery-picture-padding);
    left: var(--gallery-picture-padding);
    width: calc(100% - var(--gallery-picture-padding) * 2);
    height: calc(var(--gallery-picture-height) - var(--gallery-picture-padding) * 2);
    background: white;
    
  
    z-index: 1;
    
}
.plus {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    
    display: block;
    
    width: calc(100% - var(--gallery-picture-padding) * 2);
    height: 240px;
    
    font-size: 60px;
    text-align: center;
    line-height: 240px;
    
    color: white;
    
    pointer-events: none;
    opacity: 0;
    z-index: 2;
}

.curtain:hover ~ .plus {
    opacity: 1;
}

.galleryPictureWrapper:before {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border: 5px solid var(--blue);
}

.galleryPicture {
/*
    width: 250px;
    height: auto;
*/
}

.galleryCurtain {
    position: fixed;
    top: 0;
    left: 0;
    
    width: 100%;
    width: 100vw;
    height: 100%;
    height: 100vh;
    
    
    background: #000;
    opacity: 0.7;
    z-index: 60;
}

.galleryScreen {
    position: fixed;
    top: 50%;
    left: 50%;
    
    transform: translateX(-50%) translateY(-50%);
    
    display: block;
    
    width: 667px;
    width: var(--gallery-screen-max-width);
    height: var(--gallery-screen-height);
    max-width: var(--gallery-screen-max-width);
    max-height: var(--gallery-screen-height) !important;
    
    margin: auto;
    padding: calc(var(--gallery-picture-padding) * 2);
    border-radius: var(--gallery-screen-border-radius);
    
    background: white;
    z-index: 65;
}
.galleryScreen img {
    max-width: 667px;
    max-height: 500px;
}

.galleryScreen > div {
    position: absolute;
    background: white;
}

.leftPicture:hover, .rightPicture:hover {
    color: #eee;
}

.closeGalleryScreen {
    cursor: pointer;   
}
.closeGalleryScreen img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: calc(var(--gallery-screen-close-handle-size) / 2);
}
.closeGalleryScreen img:hover {
    opacity: 0.3;
}

.galleryScreen .closeGalleryScreen {
    top: calc(-1 * var(--gallery-screen-close-handle-size) / 2);
    right: calc(-1 * var(--gallery-screen-close-handle-size) / 2);
    
    width: var(--gallery-screen-close-handle-size);
    height: var(--gallery-screen-close-handle-size);
    border-radius: 50%;
    
    line-height: var(--gallery-screen-close-handle-size);
    text-align: center;
    
    box-shadow: -0.5px 1px 3px 0px var(--light-black);
    z-index: 70;
}
.galleryScreen .leftPicture, .galleryScreen .rightPicture {
    top: calc((var(--gallery-screen-height) - var(--gallery-screen-control-height)) / 2);
    
    top: 50%;
    transform: translateY(-50%);
    
    width: var(--gallery-screen-control-width);
    height: var(--gallery-screen-control-height);
    line-height: var(--gallery-screen-control-height);
    text-align: center;
    cursor: pointer;
}
.galleryScreen .leftPicture {
    left: calc(-1 * var(--gallery-screen-control-width) + 1px);
    border-radius: var(--gallery-screen-border-radius) 0 0 var(--gallery-screen-border-radius);
}
.galleryScreen .leftPicture > img, .galleryScreen .rightPicture > img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    max-width: 18px;
}
.galleryScreen .rightPicture > img {
    padding-right: 5px;
    
}
.galleryScreen .leftPicture > img {
    padding-left: 5px;
}
.galleryScreen .leftPicture > img:hover {
    opacity: 0.3;
}
.galleryScreen .rightPicture > img:hover {
    opacity: 0.3;
}



.galleryScreen .rightPicture {
    right: calc(-1 * var(--gallery-screen-control-width) + 1px);
    border-radius: 0 var(--gallery-screen-border-radius) var(--gallery-screen-border-radius) 0;
}

.galleryScreen > img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    max-width: var(--gallery-screen-max-width);
    max-height: var(--gallery-screen-height);
    margin: auto;
} 

.galleryPicture, .curtain {
    -webkit-transition: all .5s ease-in-out;
    -khtml-transition: all .5s ease-in-out;
    -moz-transition: all .5s ease-in-out;
    -ms-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.galleryScreen {
    -webkit-transition: all .3s ease-in-out;
    -khtml-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}




.galleryPicture.loaded ~ .lds-roller, .hidden.loaded ~ .lds-roller {
    display: none;
}


.galleryPicture.loaded ~ .curtain {
    background: #000;
    opacity: 0;
}
.galleryPicture.loaded ~ .curtain:hover {
    opacity: 0.6;
}



.lds-roller {
  position: absolute;
  top: 50%;
  left: 50%;
    
  transform: translateX(-50%) translateY(-50%);
    
  display: inline-block;
  width: 64px;
  height: 64px;
    
  z-index: 1;

}
.lds-roller div {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 32px 32px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #333;
  margin: -3px 0 0 -3px;
}
.lds-roller div:nth-child(1) {
  animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 50px;
  left: 50px;
}
.lds-roller div:nth-child(2) {
  animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 54px;
  left: 45px;
}
.lds-roller div:nth-child(3) {
  animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 57px;
  left: 39px;
}
.lds-roller div:nth-child(4) {
  animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 58px;
  left: 32px;
}
.lds-roller div:nth-child(5) {
  animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 57px;
  left: 25px;
}
.lds-roller div:nth-child(6) {
  animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 54px;
  left: 19px;
}
.lds-roller div:nth-child(7) {
  animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 50px;
  left: 14px;
}
.lds-roller div:nth-child(8) {
  animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 45px;
  left: 10px;
}
@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.hidden {
    opacity: 0;
}

.hidden.loaded {
    opacity: 1;
}