:root {
    --service-width: 460px;
	--service-height: 256px;

	--grid-gap: 70px;
    
    --service-icon-width: 164px;
}

.ServiceList {
    
}

.ServiceListItem {
    position: relative;
    padding: 0 var(--section-horizontal);
    margin-bottom: var(--section-vertical);
}
.ServiceListItem .anchor {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.ServiceListItem > div {
    
}
.ServiceListItem > div > .icon {
    display: inline-block;
}
.ServiceListItem > div > .icon > img {
    width: var(--service-icon-width);
    margin-right: calc(var(--section-horizontal) / 2);
    vertical-align: middle;
}
.ServiceListItem > div > .text {
    display: inline-block;
    --temp-width: calc((var(--section-horizontal) / 2) + var(--service-icon-width));
    width: calc(100% - (var(--temp-width)));
    vertical-align: middle;
}






div.Services {
	max-width: 1040px;
	margin: 0 auto;

	padding: 0 25px;

	display: grid;
	justify-items: stretch;

	grid-gap: var(--grid-gap);
	grid-template-columns: repeat(2, 1fr);

    margin-bottom: var(--section-vertical);
}
div.Services div.service {
	max-width: var(--service-width);
	min-height: var(--service-height);
	text-align: center;
	line-height: var(--service-height);
	padding: 20px;

	word-break: normal;

	background-color: var(--blue);
	
}
div.service > h1, div.service > h2, div.service > h3, div.service > h4 {
	display: inline-block;

	font-family: var(--service-font);
	font-size: 32px;
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 2px;
	vertical-align: middle;
	
	color: white;
	line-height: 1.2;
}
div.Services a:nth-child(2) div.service, div.Services a:nth-child(3) div.service {
	background: var(--red) !important;
}
div.Services div.service:hover {
	margin-top: -6px;
	margin-left: -6px;
	box-shadow: 6px 6px 2px #ccc;
	cursor: pointer;
}

.service {
    -webkit-transition: all 1s ease-in-out;
    -khtml-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -ms-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1180px) {
    :root {
        --grid-gap: 30px;
    }
}

@media screen and (max-width: 770px) {
    :root {
        --service-width: 100%;
    }
    div.Services {
        grid-template-columns: 1fr;
    }
    
    div.Services > a:nth-child(odd) > div.service {
		background: var(--blue) !important;
	} 

	div.Services > a:nth-child(even) > div.service {
		background: var(--red) !important;
	}
}

@media screen and (max-width: 350px) {
    div.Services > a > div.service > h1 {
        font-size: 26px !important;
    }
}
