:root {
    --nav-horizontal: calc((var(--logo-wrapper-width) - var(--logo-width)) / 2);
}
@media screen and (min-width: 1920px) {
    .gallery {
        width: calc(100% - var(--page-margin) * 2) !important;
        width: 1285px !important;
    }   
}

@media screen and (max-width: 1900px) {
    .gallery {
        width: 1030px !important;
    }
}

@media screen and (max-width: 1650px) {
    
}

@media screen and (max-width: 1244px) {
    :root {
        --hamburger-scale: 0.65;
        --nav-footer-padding: var(--nav-horizontal);
        
        --nav-height: 80px;
        --logo-width: 170px;
      
    }
    
    .gallery {
        width: 780px !important;
    }
    
    .hamburger {
        display: block;
        right: var(--nav-horizontal);
        pointer-events: auto;
    }
    
    .logoWrapper {
        top: -23px;
        left: 0;
        height: var(--nav-height);
        transform: scale(0.5);
    }

    .navigationLinks {
        position: absolute;
        top: calc(var(--nav-height) + 77px);
        right: 10px;
        
        flex-direction: column;
        font-size: 18px;
    
        pointer-events: none;
        opacity: 0;
    }
    .navigationLinks.active {
        opacity: 1;
        pointer-events: auto;
    }
    .navigationLinks.active li {
        background: var(--blue);
        height: 50px;
        opacity: 1;
    }
    .navigationLinks li {
        margin: 0 !important;
        margin-bottom: 1px !important;
        padding: 0 90px;
        
        height: 0px;
        line-height: 50px;
        
        opacity: 0;
        
        text-align: center;

        border-bottom: 1px solid var(--grey);
    }
    .navigationLinks li:last-child {
        border: none;
    }
    
    .navigationLinks li a {
        color: white;
    }
    .navigationLinks li a:hover, .navigationLinks li a.active {
        color: white;
        text-decoration: underline;
    }
}

@media screen and (max-width: 1172px) {
    
}

@media screen and (max-width: 1160px) {
    :root {
        --section-horizontal: 10%;
    }
    .ServiceListItem > div > .icon {
        width: 100%;
        text-align: center;
        margin-bottom: 25px;
    }
    .ServiceListItem > div > .icon > img {
        margin: 0 !important;
    }
    .ServiceListItem > div > .text {
        max-width: none !important;
        width: 100% !important;
    }
    .ServiceListItem > div > .text > h1:after {
        left: calc((100% - 120px) / 2);
    }
    
    .ServiceListItem > div > .text > .title {
        margin-left: 0 !important;
        text-align: center;
    }
}

@media screen and (max-width: 1069px) {
    .contact {
        flex-direction: column;
        text-align: left;
    }
    
    .contactInfo .iconWithText {
        padding-left: 0;
        width: 384px;
        margin: auto;
        margin-bottom: var(--icon-with-text-margin-bottom);
    }
    .contactInfo .iconWithText:last-child {
        margin-bottom: 5px;
    }
    
    .contactInfo {
        padding-top: 0px;
        flex: 0 0 100%;
    }

    .contactForm {
        max-width: 100% !important;
        width: 100% !important;
    }
    
    .contactFormWrapper {
        margin-top: var(--section-vertical) !important;
        flex: 0 0 100%;
    }

}

@media screen and (max-width: 883px) {
    :root {
        --page-margin: 90px;
        --icon-margin: 50px;
        --table-list-distance: 60px;
    }
    
    .gallery {
        width: 515px !important;
    }
}

@media screen and (max-width: 840px) {
    
}

@media screen and (max-width: 800px) { 
    .telLink, .mailLink {
    
    } 
    
    .datenschutz p, .impressum p {
        max-width: 100%;
    }
}

@media screen and (max-width: 768px) {

}

@media screen and (max-width: 742px) {
    
}

@media screen and (max-width: 745px) {
    :root {
        --footer-height: 70px;
    }
    .footer {
        flex-direction: column;
        text-align: center;
        line-height: 22px;
        padding: 0;
    }
    
    .footerText {
        padding: 0;
        padding-top: 13px;
        height: 20px;
    }
    
    .footerLinks {
        padding: 0;
        padding-bottom: 13px;
        height: 20px;
    }
}

@media screen and (max-width: 700px) {
    .gallery {
        width: 258px !important;
        margin: 70px auto;
    }
    
    .galleryPictureWrapper {
        margin: 20px 0;
    }
    
    h1 {
        font-size: 32px;
    }
}
@media screen and (max-width: 510px) {
    :root {
        --page-top-margin: 45px;
        --section-horizontal: 7%;
        --nav-horizontal: 25px;
    }
    
    .logoWrapper {
        transform: scale(.5);
        left: -70px;
    }
    
    .iconWithText {
        width: 100% !important;
    }
    .iconWithText img {
        margin: 0 !important;
        margin-bottom: 15px !important;
    }
    .iconWithText:last-child img {
        margin: 0 !important;
    }
    .iconWithText .textWrapper {
        width: 100%;
        padding: 0 !important;
    }
    .iconWithText {
        text-align: center;
        padding: 0 !important;
        margin: 0;
    }
}

@media screen and (max-width: 414px) {
    :root {
        --header-margin: 30px;
        --page-margin: 30px;
        --nav-footer-padding: 20px;
        
        --hamburger-scale: .7;
        
        --section-vertical: 50px;
        --title-vertical: 35px;
    }
        
    .gallery {
        margin: var(--section-vertical) auto !important;
    }
    
    h1 {
        
    }
    
    .list {
        margin-top: var(--list-style-margin);
        flex-direction: column;
        height: auto;
    }
    
    
    .navigationLinks {
        width: 100%;
        left: 0px;
        top: 143px;
    }
    
    .tableList {

    }
    
    .galleryScreen {
        width: 100%;
        padding: 0;
        height: 100vh;
        display: block;
        border-radius: 0 !important;
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
    
    .leftPicture, .rightPicture {
        height: 40px !important;
        background: transparent !important;
        display: none;
    }
    .leftPicture img, .rightPicture img {
        filter: brightness(0) invert(1);
    }
    
    .leftPicture {
        left: 0 !important;
    }
    .rightPicture {
        right: 0 !important;
    }
    
    .closeGalleryScreen {
        right: 13px !important;
    }
    
    .galleryScreen > .lds-roller {
        display: none;
    }
    
    .galleryScreen > img {
        width: 100% !important;
    }
    
    .datenschutz h3 {
        font-size: 27px;
        word-break: break-all;
    }
    
    .datenschutz a {
        word-break: break-all;
    }
}

@media screen and (max-width: 375px) {
    :root {
        --gallery-padding-top: 30px;
        --footer-font-size: 13px;
    }
    
    .list .listItem {
        font-size: 14px;
    }
}
@media screen and (max-width: 345px) {
    :root {
        --footer-font-size: 11px;
    }
}

@media screen and (max-width: 320px) {
    :root {
      --nav-height: 80px;
      --nav-font-size: 14px;
      --gallery-padding-top: 5px;
      --hamburger-scale: .55;  
    }
    
    .logoWrapper {
        transform: scale(.41);
        top: -18px;
        left: -90px;
    }
    
     h1 {
        font-size: 28px;
    }
}