:root {
    --icon-with-text-size: 49px;
    --icon-with-text-margin-right: 28px;
    --icon-with-text-margin-bottom: 50px;
    
    --contact-font-weight: 500;
    
    --form-input-height: 50px;
    --form-textarea-height: 160px;
    
    --form-hint-font-size: 16px;
}

.contact {
    display: flex;
    padding: 0 calc(var(--section-horizontal) / 2);
    margin-bottom: var(--section-vertical);
}

.contactInfo {
    padding-top: 19px;
    flex: 0 0 50%;
}

.contactFormWrapper {
    flex: 0 0 50%;
}

.contactInfo .iconWithText {
    padding-left: 10%;
    margin-bottom: var(--icon-with-text-margin-bottom);
    white-space: pre-line;
}
.contactInfo .iconWithText img {
    max-width: var(--icon-with-text-size);
    max-height: var(--icon-with-text-size);
    vertical-align: middle;
    
    margin-right: var(--icon-with-text-margin-right);
}
.textWrapper, .textWrapper a {
    display: inline-block;
    font-family: var(--contact-font);
    font-size: 21px !important;

    font-weight: var(--contact-font-weight) !important;
    font-weight: 100;
    color: var(--red);
}
.contactInfo .iconWithText:last-child {
    margin-top: -10px;
}
.contactInfo .iconWithText:last-child .textWrapper {
    position: relative;
    line-height: 1.5;
    top: 16px;
}



.contactForm {
    display: flex;
    flex-direction: column;
    max-width: 90%;
}

.contactForm label {
    position: relative;
}

input.hasText, textarea.hasText {
    border-bottom: 2px solid var(--blue) !important;
}
input, textarea {
    border: none;
    border-bottom: 2px solid var(--blue);
}
input:focus, textarea:focus {
    border-color: var(--nav-pink);
}
.contactForm label input {
    width: 100%;
    height: var(--form-input-height);
}
.contactForm label textarea {
    width: 100%;
    height: var(--form-textarea-height);
    padding: 15px 0;
    resize: none;
}
.contactForm label .formInputHint {
    position: absolute;
    top: 0;
    left: 0;
    
    z-index: 1;
    color: var(--nav-pink);
    font-size: var(--form-hint-font-size);
    font-weight: var(--paragraph-font-weight);
    line-height: var(--form-input-height);
    pointer-events: none;
}
.contactForm label input:focus ~ .formInputHint, .contactForm label textarea:focus ~ .formInputHint, .formInputHint.off {
    top: -16px !important;
    font-size: 10px;
    opacity: 0;
}

.formSubmitButton {
    width: 40%;
    height: calc(var(--form-input-height) / 1.2);
    margin-top: 25px;
    
    align-self: flex-end;
    font-size: 15.5px;
    font-weight: bold;
    
    color: white;
    border: none;
    background-color: var(--blue);
    cursor: pointer;
}
.formSubmitButton:hover {
    background: var(--red);
}

input.invalid {
    border-bottom: 3px solid red;
   
}
.validInput, .invalidInput {
    position: absolute;
    top: 13px;
    right: 0;
    
    width: 24px;
    height: 24px;
    
    background-size: contain;
}

.validInput {
    background-image: url('/icons/valid.svg');
    opacity: 0;
}

.invalidInput {
    background-image: url('/icons/invalid.svg');
    opacity: 0;
}

.formStatus {
    position: fixed;
    top: 50%;
    left: 50%;
    
    transform: translateX(-50%) translateY(-50%);
    
    width: 30%;
    height: 100px;
    text-align: center;
    line-height: 100px;
    
    pointer-events: none;
    
    border: 1px solid var(--nav-blue);
    background: white;
    
    color: var(--nav-pink);
    
    z-index: 6;
    opacity: 0;
}
.formMessages {
    white-space: pre-line;
    color: red;
    
    font-size: 12px;
    font-weight: 600;
    line-height: 16px;
    
    text-align: center;
    
    padding: 10px;
    
    background: var(--grey);
    border-right: 5px solid #eee;
    display: none;
    
}
.formMessages:before {
    content: '! ';
}
.formMessages:after {
    content: ' !';
}