html, body, header, footer, article, section, h1, h2, h3, h4, h5, h6, div, span, p, ul, ol, li, form, input, textarea, a {
  margin: 0;
  padding: 0;
  outline: none;
}

a { text-decoration: none; }
ul, ol { list-style: none; }

.impressum {
    min-height: 100vh;
}

:root {
    --text-color: #292929;
    --blue: #000066;
    --red: #ED0000;
    --white: #FFFFFF;

    --text-font: 'Roboto';

    --big: 46px;
    --med: 25px;
    --sma: 20px;
    
    /* SPACES */
    --section-vertical: 100px;
    --section-horizontal: 184px;
    --title-vertical: 76px;
    
    /* NAVIGATION */
    --nav-space: 91px;
    --nav-height: 120px;
    --nav-font: 'Myriad Pro';
    --nav-font-size: 26px;
    --nav-font-weight: 500;
    
    --nav-footer-padding: 120px;
    
    --heading-font: 'Pragmatica Extended', sans-serif;
    --service-font: 'Pragmatica Extended', sans-serif;
    --contact-font: 'Pragmatica Extended', sans-serif;
    
    --hamburger-width: 79px;
    --hamburger-line-height: 6px;
    --hamburger-line-margin: 11.5px;
    --hamburger-height: calc((var(--hamburger-line-height) * 3) + (var(--hamburger-line-margin) * 2));
    --hamburger-scale: 1;
    
    --logo-wrapper-width: 300px;
    --logo-wrapper-height: 175px;
    --logo-wrapper-border-radius: 30px; 

   
    --logo-width: 180px;
    --logo-height: 137px;
    --logo-scale: 1;
    
    /* FOOTER */
    --footer-height: 50px;
    --footer-font: 'Roboto', sans-serif;
    --footer-font-size: 14px;
    --footer-link-space: 36px;
}

html, body {
    overflow-x: hidden;
    color: #292929;
    color: var(--text-color);
}


/* NAVIGATION */

body {
    font-family: 'Roboto';
    font-family: var(--text-font);
    font-size: 20px;
    font-size: var(--sma);
    
    padding-top: 120px;
    
    padding-top: var(--nav-height);
}

.navigation.top {
    top: calc(-1 * 120px);
    top: calc(-1 * var(--nav-height));
}

.navigation {
    position: fixed;
    top: 0;
    left: 0;
    
    width: 100%;
    height: 120px;
    height: var(--nav-height);
    background: white;
    z-index: 10;
}
.navigation, .navigationLinks {
    height: 120px;
    height: var(--nav-height);
}

.navigationLinks {
    display: flex;
    justify-content: center;
    
    font-family: 'Myriad Pro';
    
    font-family: var(--nav-font);
    line-height: 120px;
    line-height: var(--nav-height);
    font-size: 26px;
    font-size: var(--nav-font-size);
    font-weight: 500;
    font-weight: var(--nav-font-weight);
    
    text-transform: capitalize;
/*    padding: 0 var(--nav-footer-padding);*/
    padding: 0;
}
.navigationLinks li:nth-child(1) {
    margin-right: 91px;
    margin-right: var(--nav-space);
}
.navigationLinks li:nth-child(2) {
    margin-right: calc(300px / 2 + 91px);
    margin-right: calc(var(--logo-wrapper-width) / 2 + var(--nav-space));
}
.navigationLinks li:nth-child(3){
    margin-left: calc(300px / 2 + 91px);
    margin-left: calc(var(--logo-wrapper-width) / 2 + var(--nav-space));
} 
.navigationLinks li:nth-child(4) {
    margin-left: 91px;
    margin-left: var(--nav-space);
}
.navigationLinks > li > a {
    color: #000066;
    color: var(--blue);
}
.navigationLinks > li > a:hover {
    color: #ED0000;
    color: var(--red);
}

.navigationLinks > li > a.active {
    color: #ED0000;
    color: var(--red);
}

.logo {
    position: absolute;
    
    top: calc((175px - 137px) / 2);
    
    top: calc((var(--logo-wrapper-height) - var(--logo-height)) / 2);
    left: calc((300px - 180px) / 2);
    left: calc((var(--logo-wrapper-width) - var(--logo-width)) / 2);
    width: 180px;
    width: var(--logo-width);
    
}
.logoWrapper {
    position: absolute;
    left: calc((100% - 300px) / 2);
    left: calc((100% - var(--logo-wrapper-width)) / 2);
    
    width: 300px;
    
    width: var(--logo-wrapper-width);
    height: 175px;
    height: var(--logo-wrapper-height);
    
    background: white;
    border-radius: 30px;
    border-radius: var(--logo-wrapper-border-radius);
    
    z-index: 5;
}
.scaleLogo {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    top: -30px;
}

/* HAMBURGER */

.hamburger {
    position: absolute;
    top: calc((120px + (-1 * calc((6px * 3) + (11.5px * 2)))) / 2);
    top: calc((var(--nav-height) + (-1 * var(--hamburger-height))) / 2);
    right: calc((300px - 180px) / 2);
    right: calc((var(--logo-wrapper-width) - var(--logo-width)) / 2);
    
    cursor: pointer;
    pointer-events: none;
    display: none;
    
    -webkit-transform: scale(1);
    
            transform: scale(1);
    
    -webkit-transform: scale(var(--hamburger-scale));
    
            transform: scale(var(--hamburger-scale));
    
}

.hamburger div {
    width: 79px;
    width: var(--hamburger-width);
    height: 6px;
    height: var(--hamburger-line-height);
    margin-bottom: 11.5px;
    margin-bottom: var(--hamburger-line-margin);
    
    background: #000066;
    
    background: var(--blue);
}
.hamburger.active div { 
    background: #ED0000; 
    background: var(--red);
    -webkit-transform-origin: center;
            transform-origin: center;
}
.hamburger.active div:nth-child(1) {
    -webkit-transform: translateY(calc(11.5px + 6px)) rotate(45deg);
            transform: translateY(calc(11.5px + 6px)) rotate(45deg);
    -webkit-transform: translateY(calc(var(--hamburger-line-margin) + var(--hamburger-line-height))) rotate(45deg);
            transform: translateY(calc(var(--hamburger-line-margin) + var(--hamburger-line-height))) rotate(45deg);
}
.hamburger.active div:nth-child(2) {
    transition: none;
    width: 0;
    opacity: 0;
}
.hamburger.active div:nth-child(3) {
    -webkit-transform: translateY(calc(-1 * (11.5px + 6px))) rotate(-45deg);
            transform: translateY(calc(-1 * (11.5px + 6px))) rotate(-45deg);
    -webkit-transform: translateY(calc(-1 * (var(--hamburger-line-margin) + var(--hamburger-line-height)))) rotate(-45deg);
            transform: translateY(calc(-1 * (var(--hamburger-line-margin) + var(--hamburger-line-height)))) rotate(-45deg);
}


/* FOOTER */

.footer {    
    display: flex;
    justify-content: space-between;

    height: 50px;

    height: var(--footer-height);
    
    padding-left: 120px;
    
    padding-left: var(--nav-footer-padding);
    padding-right: calc(120px - 14px);
    padding-right: calc(var(--nav-footer-padding) - 14px);
    
    font-size: 14px;
    
    font-size: var(--footer-font-size);
    line-height: 50px;
    line-height: var(--footer-height);
    
    color: #FFFFFF;
    
    color: var(--white);
    background: #000066;
    background: var(--blue);
}
.footerLinks > a {
    color: #FFFFFF;
    color: var(--white);
    font-family: 'Roboto', sans-serif;
    font-family: var(--footer-font);
    padding: 0 calc(36px / 2);
    padding: 0 calc(var(--footer-link-space) / 2);
}
.footerLinks > a:hover, .footerLinks > a.active {
    font-family: 'Roboto', sans-serif;
    font-family: var(--footer-font);
    text-decoration: underline;
}

/* OTHER */

.title {
    position: relative;
    font-family: 'Pragmatica Extended', sans-serif;
    font-family: var(--service-font);
    font-size: 46px;
    font-size: var(--big);
    font-weight: 500;
    margin-bottom: 76px;
    margin-bottom: var(--title-vertical);
}
.title:after {
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 120px;
    height: 4px;
    background: #ED0000;
    background: var(--red);
    content: '';
}

.navigation, .navigationLinks, .navigationLinks > li > a, .hamburger div, .anim, .logoWrapper, input, textarea, .formInputHint, .formSubmitButton {
    transition: all .5s ease-in-out;
}


