.ServiceListItem > div {
    
}
.ServiceListItem > div > .icon {
    display: inline-block;
}
.ServiceListItem > div > .icon > img {
    width: 164px;
    width: var(--service-icon-width);
    margin-right: calc(var(--section-horizontal) / 2);
