}






div.Services {
	max-width: 1040px;
	margin: 0 auto;

	padding: 0 25px;

	display: grid;
	justify-items: stretch;

	grid-gap: 70px;

	grid-gap: var(--grid-gap);
	grid-template-columns: repeat(2, 1fr);

    margin-bottom: var(--section-vertical);
}
div.Services div.service {
	max-width: 460px;
	max-width: var(--service-width);
	min-height: 256px;
	min-height: var(--service-height);
	text-align: center;
	line-height: 256px;
	line-height: var(--service-height);
	padding: 20px;

	word-break: normal;

	background-color: var(--blue);
	
}
div.service > h1, div.service > h2, div.service > h3, div.service > h4 {
	display: inline-block;

	font-family: var(--service-font);
	font-size: 32px;
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 2px;
	vertical-align: middle;
	
	color: white;
	line-height: 1.2;
}
div.Services a:nth-child(2) div.service, div.Services a:nth-child(3) div.service {
	background: var(--red) !important;
}
div.Services div.service:hover {
	margin-top: -6px;
	margin-left: -6px;
	box-shadow: 6px 6px 2px #ccc;
	cursor: pointer;
}

.service {
    transition: all 1s ease-in-out;
}

