    background: white;
}

.leftPicture:hover, .rightPicture:hover {
    color: #eee;
}

.closeGalleryScreen {
    cursor: pointer;   
}
.closeGalleryScreen img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    width: calc(44px / 2);
    width: calc(var(--gallery-screen-close-handle-size) / 2);
}
.closeGalleryScreen img:hover {
    opacity: 0.3;
}

.galleryScreen .closeGalleryScreen {
    top: calc(-1 * 44px / 2);
    top: calc(-1 * var(--gallery-screen-close-handle-size) / 2);
    right: calc(-1 * 44px / 2);
    right: calc(-1 * var(--gallery-screen-close-handle-size) / 2);
    
    width: 44px;
    
    width: var(--gallery-screen-close-handle-size);
    height: 44px;
    height: var(--gallery-screen-close-handle-size);
    border-radius: 50%;
    
    line-height: 44px;
    
    line-height: var(--gallery-screen-close-handle-size);
    text-align: center;
    
    box-shadow: -0.5px 1px 3px 0px var(--light-black);
    z-index: 70;
}
.galleryScreen .leftPicture, .galleryScreen .rightPicture {
    top: calc((500px - 110px) / 2);
    top: calc((var(--gallery-screen-height) - var(--gallery-screen-control-height)) / 2);
    
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    
    width: 35px;
    
    width: var(--gallery-screen-control-width);
    height: 110px;
    height: var(--gallery-screen-control-height);
    line-height: 110px;
    line-height: var(--gallery-screen-control-height);
    text-align: center;
    cursor: pointer;
}
.galleryScreen .leftPicture {
    left: calc(-1 * 35px + 1px);
    left: calc(-1 * var(--gallery-screen-control-width) + 1px);
    border-radius: 15px 0 0 15px;
    border-radius: var(--gallery-screen-border-radius) 0 0 var(--gallery-screen-border-radius);
}
.galleryScreen .leftPicture > img, .galleryScreen .rightPicture > img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
            transform: translateY(-50%) translateX(-50%);
    max-width: 18px;
}
.galleryScreen .rightPicture > img {
    padding-right: 5px;
    
}
.galleryScreen .leftPicture > img {
    padding-left: 5px;
}
.galleryScreen .leftPicture > img:hover {
    opacity: 0.3;
}
.galleryScreen .rightPicture > img:hover {
    opacity: 0.3;
}



.galleryScreen .rightPicture {
    right: calc(-1 * 35px + 1px);
    right: calc(-1 * var(--gallery-screen-control-width) + 1px);
    border-radius: 0 15px 15px 0;
    border-radius: 0 var(--gallery-screen-border-radius) var(--gallery-screen-border-radius) 0;
}

.galleryScreen > img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    max-width: 800px;

    max-width: var(--gallery-screen-max-width);
    max-height: 500px;
    max-height: var(--gallery-screen-height);
    margin: auto;
} 

.galleryPicture, .curtain {
    transition: all .5s ease-in-out;
}

.galleryScreen {
    transition: all .3s ease-in-out;
}




.galleryPicture.loaded ~ .lds-roller, .hidden.loaded ~ .lds-roller {
    display: none;
}


.galleryPicture.loaded ~ .curtain {
    background: #000;
    opacity: 0;
}
.galleryPicture.loaded ~ .curtain:hover {
    opacity: 0.6;
}



.lds-roller {
  position: absolute;
  top: 50%;
  left: 50%;
    
  -webkit-transform: translateX(-50%) translateY(-50%);
    
          transform: translateX(-50%) translateY(-50%);
    
  display: inline-block;
  width: 64px;
  height: 64px;
    
  z-index: 1;

}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 32px 32px;
          transform-origin: 32px 32px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #333;
  margin: -3px 0 0 -3px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 50px;
  left: 50px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 54px;
  left: 45px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 57px;
  left: 39px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 58px;
  left: 32px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 57px;
  left: 25px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 54px;
  left: 19px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 50px;
  left: 14px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 45px;
  left: 10px;
}
@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

