.Slider {
    position: relative;
    height: 200px;
    width: 100%;
    overflow: hidden;
}
.relativeWrapper {
    position: relative;
    height: 498px;
    overflow: hidden;
}
.BigPicture {
    position: absolute;
    width: 100%;

    top: 0;
    left: 0;
}

.BigPicture.active {
    opacity: 1;
    z-index: 2;
}

.BigPicture.inactive {
    opacity: 0;
    z-index: 1;
}

.BigPicture {
    -webkit-transition: opacity 1s ease-in-out;
    -khtml-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -ms-transition: opacity 1s ease-in-out;
    transition: opacity 1s ease-in-out;
}

.relativeWrapper {
    margin-bottom: var(--section-vertical);
}