.preLoader {
    position: fixed;
    top: 0;
    left: 0;
    
    background: white;
    
    width: 100%;
    height: 100vh;
    
    opacity: 0;
    pointer-events: none;
    z-index: 500;
}

.preLoader.active {
    pointer-events: auto;
    opacity: 1;
}

.wheel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);

  width: 50px;
  height: 50px;
  
  border-radius: 50%;
  border: 5px solid var(--red);
  
  animation: rotation 0.5s infinite linear;
}

.wheel:after {
  display: block;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: white;
  position: relative;
  content: '';
  top: -8px;
  left: -8px;

}

@keyframes rotation {
  from {
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
  }
  to {
    transform: translateX(-50%) translateY(-50%) rotate(360deg);
  }
}